@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}

.header{display:flex;align-items:center;justify-content:space-between;padding:1rem 2.5rem;border-bottom:1px solid lightgray;position:-webkit-sticky;position:sticky;top:0;background-color:white}.header .header__logoImage{width:100px;object-fit:contain}.header .headerSearch{display:flex;align-items:center;flex:1 1;justify-content:center}.header .headerSearch .header__searchInputContainer{display:flex;align-items:center;padding:0.8rem 1rem 0.8rem 2rem !important;border-radius:50px;text-transform:inherit;background-color:white;text-align:left;-webkit-animation-name:example;animation-name:example;-webkit-animation-duration:0.2s;animation-duration:0.2s;width:300px}.header .headerSearch .header__searchInputContainer .MuiButton-label{display:flex;justify-content:space-between}.header .headerSearch .header__searchInputContainer .header__searchIconContainer{display:flex;align-items:center;background-color:#FF385C;border-radius:100px}.header .headerSearch .header__searchInputContainer .header__searchIconContainer .MuiSvgIcon-root{color:white}.header .headerRight{display:flex;align-items:center}.header .headerRight .header__hostButton{background-color:white;text-transform:inherit;border:none;box-shadow:none;border-radius:30px}.header .headerRight .header__hostButton:hover{background-color:#f3f3f3}@-webkit-keyframes example{from{width:500px}to{width:300px}}@keyframes example{from{width:500px}to{width:300px}}

.banner{height:500px;padding:50px 100px;background-position:center}.banner .bannerSearch{width:100%;height:100px;background-color:white;border-radius:50px;display:flex;align-items:center;justify-content:center}


